	module.exports = {"data":{"site":{"siteMetadata":{"title":"Dan Beaven's blog","author":"Dan Beaven"}},"markdownRemark":{"id":"/Users/dan/Code/blog/src/pages/posts/02-modernism-scotish-school.md absPath of file >>> MarkdownRemark","html":"<h1>Modernism and Scottish school</h1>\n<p>In this week’s lecture we covered seven of the major art movements in the 20th Century, Scottish School, Art Nouveau, Modernism, Art Deco, Surrealism, Cold War Design and Postmodernism. In the essay I look closer at Scottish school and modernism using their most iconic chairs.</p>\n<h2>Scottish school</h2>\n<p>Scottish school was fashionable between 1885 and 1915, formed in glasgow and edinburgh it aimed to produce work which improved contemporary cosmopolitan life (Victoria, Museum, 2013b). One of the most prominent artists of the time was Charles Rennie Mackintosh whose chair is pictured on the right.</p>\n<p>Scottish school is famous for geometric shapes and combining straight lines with curves (Victoria, Museum, 2013b). This can be seen in the back of this chair which has a rectangular footprint but is framed by two legs which start of square but elegantly taper to circular. The back also has an interesting symmetry between the oval head rest and the oval curve of the support at the bottom.<img src=\"https://lh4.googleusercontent.com/yDNP0ibmtWRx-lGt9Qio4yaxbF_QGMw4Q0xgMN-RzHHO6pfGHoCcASilWzLN14FIwa3owMzgHb-xg8rSMWGL-YdKwS8kp3ahNUAmVA37j5MY6tiOo_e8oa-Vn1wlYd0nOzDzLqcd\" alt=\"circ.130-1958_chair_1000px.jpg\"></p>\n<h2>Modernism</h2>\n<p>Modernism is a post second world war movement notable for the philosophical mindset that technology and design could change society. They believed in design for the masses and this is seen in the style, which is often minimal and geometric (Victoria, Museum, 2013a).</p>\n<p>Wassily chair by Marcel Breuer was the obvious choice for c to construct a folding and none folding chair. The use of industrial materials and the relatively ma modernist chair. Inspired by the design of his motorbike Breuer used chrome plated steel tubes and minimal design mean it can be manufactured at scale (La Fonda del Sol 2012).</p>\n<p>Mackintosh’s chair like Breuer’s uses simple geometric shapes, the main and key difference is in the choice of materials. Mackintosh’s chair would be much harder to produce at scale it also lacks in my eyes the aesthetic balance the Breuer’s has.<img src=\"https://lh6.googleusercontent.com/71lc94XYmJR_fqO4_1oDA8nfe9XSawjUHpGHvaSWj5kY_u1c0ZuGR62F0ybI1zSDliWQVuSri8SabzzMMWtboLPAQQvXwTpflFzToJw8J5hN7wpf18wbOehBhaNcA1XMSrbJzhFx\" alt=\"B3Wassily_Breuer_02.jpg\"></p>\n<h4>References</h4>\n<p>La Fonda del Sol – exhibition tour &#x26; dinner / SPECIAL (DE) (2012) Available at: <a href=\"http://www.design-museum.de/en/collection/100-masterpieces/detailseiten/b3wassily-marcel-breuer.html\">http://www.design-museum.de/en/collection/100-masterpieces/detailseiten/b3wassily-marcel-breuer.html</a> (Accessed: 5 December 2016).</p>\n<p>Victoria, Museum, A. and webmaster, D.M. (2013a) Modernism - Victoria and Albert Museum. Available at: <a href=\"http://www.vam.ac.uk/page/m/modernism/\">http://www.vam.ac.uk/page/m/modernism/</a> (Accessed: 5 December 2016).</p>\n<p>Victoria, Museum, A. and webmaster, D.M. (2013b) Style guide: Scottish school - Victoria and Albert Museum. Available at: <a href=\"http://www.vam.ac.uk/content/articles/s/style-guide-scottish-school/\">http://www.vam.ac.uk/content/articles/s/style-guide-scottish-school/</a> (Accessed: 5 December 2016).</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\"><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">\"date\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"31/10/2016\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"images\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">\"thumbnail\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token property\">\"src\"</span><span class=\"token operator\">:</span>\n        <span class=\"token string\">\"https://lh6.googleusercontent.com/71lc94XYmJR_fqO4_1oDA8nfe9XSawjUHpGHvaSWj5kY_u1c0ZuGR62F0ybI1zSDliWQVuSri8SabzzMMWtboLPAQQvXwTpflFzToJw8J5hN7wpf18wbOehBhaNcA1XMSrbJzhFx\"</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"description\"</span><span class=\"token operator\">:</span>\n    <span class=\"token string\">\"Mackintosh’s chair like Breuer’s uses simple geometric shapes, the main and key difference is in the choice of materials.\"</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>","frontmatter":{"title":"Modernism and Scottish school","date":"October 31, 2016"}}},"pathContext":{"slug":"/posts/02-modernism-scotish-school/","previous":null,"next":{"fields":{"slug":"/posts/01-what-is-cultour/"},"frontmatter":{"title":"What is Culture?","description":"The search for a definition of culture felt like traversing a tangled web of ambiguity leading from one historical piece of literature to the next.","thumbnail":null,"gallery":null,"essay":null}}}}

